<template>
	<view class="box" v-if="type_check">
		<view class="tab-info">
			<view class="tab-li" :class="{'active':type==1}" @tap="chooseType(1)">个人身份</view>
			<view class="tab-li" :class="{'active':type==2}" @tap="chooseType(2)">商家身份</view>
		</view>
		<view class="info-li">
			<view class="left">
				<view class="iconfont iconwode"></view>
				<view class="title">
					真实姓名<text class="text-red">*</text>
				</view>
			</view>
			<input class="info-li-input" v-model="userName" type="text" placeholder="请填写真实姓名,作为身份验证">
		</view>
		<view class="info-li">
			<view class="left">
				<view class="iconfont iconshenfenzheng"></view>
				<view class="title">
					身份证号码<text class="text-red">*</text>
				</view>
			</view>
			<input class="info-li-input" maxlength="18" v-model="identityId" type="text" placeholder="请输入真实的身份证号码">
		</view>
		<view class="info-li info-photo-li">
			<view class="card-title">
				请上传有效的二代身份证
			</view>
			<view class="photo_list">
				<view class="photo_li" v-if="!url1" @tap="chooseImage('url1')">
					<view class="photo_default">
						<image lazy-load class="default_icon" src="../../static/images/photo.png" mode="aspectFill"></image>
						<view class="default_text">
							身份证正面照片
						</view>
					</view>
				</view>
				<view class="photo_li " v-else>
					<image lazy-load class="default_img" :src="img_url1" mode="aspectFill"></image>
					<view class="iconfont iconcuowu" @tap="closeImg('url1')"></view>
				</view>

				<view class="photo_li" v-if="!url2" @tap="chooseImage('url2')">
					<view class="photo_default">
						<image lazy-load class="default_icon" src="../../static/images/photo.png" mode="aspectFill"></image>
						<view class="default_text">
							身份证反面照片
						</view>
					</view>
				</view>
				<view class="photo_li " v-else @tap="closeImg('url2')">
					<image lazy-load class="default_img" :src="img_url2" mode="aspectFill"></image>
					<view class="iconfont iconcuowu"></view>
				</view>

				<view class="photo_li" v-if="!url" @tap="chooseImage('url')">
					<view class="photo_default">
						<template>
							<image lazy-load class="default_icon" src="../../static/images/photo.png" mode="aspectFill"></image>
							<view class="default_text">
								手持身份证照片
							</view>
						</template>
					</view>
				</view>
				<view class="photo_li " v-else @tap="closeImg('url')">
					<image lazy-load class="default_img" :src="img_url" mode="aspectFill"></image>
					<view class="iconfont iconcuowu"></view>
				</view>

			</view>
		</view>
		<view class="info-li info-photo-li" v-if="type==2">
			<view class="card-title">
				上传营业执照照片
			</view>
			<view class="photo_list">
				<view class="photo_li" v-if="!enterpriseUrl" @tap="chooseImage('enterpriseUrl')">
					<view class="photo_default">
						<image lazy-load class="default_icon" src="../../static/images/photo.png" mode="aspectFill"></image>
						<view class="default_text">
							营业执照照片
						</view>
					</view>
				</view>
				<view class="photo_li " v-else>
					<image lazy-load class="default_img" :src="img_enterpriseUrl" mode="aspectFill"></image>
					<view class="iconfont iconcuowu" @tap="closeImg('enterpriseUrl')"></view>
				</view>
		
			</view>
		</view>
		<view class="info-li">
			<view class="left">
				<view class="iconfont iconxingbie"></view>
				<view class="title">
					性别<text class="text-red">*</text>
				</view>
			</view>
			<view class="right">
				<radio-group name="gender" @change="radioChange">
					<label class="gender-l">
						<radio value="1" :checked='sex==1' /><text>男</text>
					</label>
					<label>
						<radio value="2" :checked='sex==2' /><text>女</text>
					</label>
				</radio-group>
			</view>
		</view>
		<view class="info-li">
			<view class="left">
				<view class="iconfont iconshoujihao"></view>
				<view class="title">
					手机号<text class="text-red">*</text>
				</view>
			</view>
			<input class="info-li-input" v-model="mobileNo" maxlength="11" type="text" placeholder="请输入真实的手机号">
		</view>
		<view class="info-li">
			<view class="left">
				<view class="iconfont iconyanzhengma"></view>
				<view class="title">
					验证码<text class="text-red">*</text>
				</view>
			</view>
			<input class="info-li-input" v-model="mobileCode" type="text" placeholder="请输入验证码">
			<view class="yzm-btn border-btn" @tap="getPhoneCode">
				{{yzm_text}}
			</view>
		</view>
		<view class="info-li">
			<view class="left">
				<view class="iconfont iconidinput"></view>
				<view class="title">
					邀请码
				</view>
			</view>
			<input class="info-li-input" v-model="invitedId" type="text" placeholder="选填">
		</view>
		<view class="info-li">
			<view class="left">
				<view class="iconfont iconyouxiang"></view>
				<view class="title">
					邮箱
				</view>
			</view>
			<input class="info-li-input" v-model="email" type="text" placeholder="选填">
		</view>
		<view class="next-btn" @tap='save'>
			支付￥{{money}}元/年入驻
		</view>

	</view>
	<view v-else class="empty">
		<image lazy-load class="empty-icon" src="../../static/images/mescroll-empty.png" mode=""></image>
		<view class="empty-tip">
			~ 暂无相关数据 ~
		</view>
	</view>
</template>

<script>
	let t = 60;
	let cut = null;
	let is_second=false; //是否再次入驻
	import {
		mapState
	} from 'vuex';
	export default {
		data() {
			return {
				money:0,
				api_url: this.$webconfig.api_url,
				invitedId:"",//邀请人id
				img_url: "",
				img_url1: "",
				img_url2: "",
				img_enterpriseUrl:"",
				"type":1,//个人身份
				"userName": "",
				"identityId": "", //身份证号码
				"mobileNo": "",
				'mobileCode': "",
				"companyProfile": "", //湖北省武汉市
				"url": "",
				"url1": "",
				"url2": "",
				"email": "",
				"sex": 1,//性别
				"enterpriseAddress": "",
				"enterpriseBusiness": "",
				"enterpriseCapital": "",
				"enterpriseCreditCode": "",
				"enterpriseUrl":"",//营业执照照片
				"enterpriseName": "",
				"merchantCode": "",
				"merchantShortCode": "",
				"remarks": "",
				"seqId": "",
				"status": "",
				userSeqId:this.$common.getLocalSync('customerSeqId'),
				yzm_text: "获取验证码",

			}
		},
		onLoad(option) {
			if(option.merchantSeqId){
				//再次入驻
				this.seqId=option.merchantSeqId||""  
				this.loadInfo();
				is_second=true;
			}else{
				// 获取商户信息
				this.findByUserSeqId();
				is_second=false;
			}
			this.invitedId=this.invite_code;
			this.loadMoney()
		},
		computed: {
			...mapState({
				type_check: state => state.type_check,
				invite_code:state=>state.invite_code
			}),
		},
		methods: {
			// 获取商户信息
			findByUserSeqId(){
				this.$api.get('/merchant/findByUserSeqId',{params:{
					userSeqId:this.$common.getLocalSync('customerSeqId')
				}}).then(res=>{
					if(res.code==200){
						let datas=res.data;
						if(datas&&datas.length>0){
							let data=datas[0];
							this.type=data.type||1,//个人身份
							this.userName= data.userName;
							this.identityId=data.identityId //身份证号码
							this.mobileNo=data.mobileNo
							this.url=data.url
							this.url1=data.url1
							this.url2=data.url2
							this.img_url=data.url
							this.img_url1=data.url1
							this.img_url2=data.url2
							this.img_enterpriseUrl=data.enterpriseUrl
							this.enterpriseUrl=data.enterpriseUrl
							this.email=data.email
							this.sex=data.sex||1//性别
							this.enterpriseName=data.enterpriseUrl
							this.seqId=data.seqId
							is_second=data.isSuccessPay
						}
					}
				})
			},
			// 再次入驻
			loadInfo(){
				this.$api.get('/merchant/Echo',{params:{
					seqId:this.seqId
				}}).then(res=>{
					if(res.code==200){
						let data=res.data;
						this.type=data.type||1,//个人身份
						this.userName= data.userName;
						this.identityId=data.identityId //身份证号码
						this.mobileNo=data.mobileNo
						this.url=data.url
						this.url1=data.url1
						this.url2=data.url2
						this.img_url=data.url
						this.img_url1=data.url1
						this.img_url2=data.url2
						this.img_enterpriseUrl=data.enterpriseUrl
						this.email=data.email
						this.sex=data.sex||1,//性别
						this.enterpriseUrl=data.enterpriseUrl
						this.enterpriseName=data.enterpriseUrl
						this.remarks=data.remarks
						this.invitedId=data.invitedId
						// 修改信息，存shop信息 下一页用
						this.$common.setLocal('shop_certificate',data.o2oMerchantShopList[0])
					}
				})
			},
			getPhoneCode() {
				let self = this;
				let phonereg = /^1[3456789]\d{9}$/
				if (!(phonereg.test(this.mobileNo))) {
					this.$box.toast('请填写正确的手机号码', 'none');
					return;
				}
				if (cut) {
					return
				}
				this.$api.get('/wechat/email/getSmsCode', {
					params: {
						mobile: this.mobileNo
					}
				}).then(res => {
					if (res.code == 200) {
						this.$box.toast(res.msg);
						cut = setInterval(function() {
							self.cutTime()
						}, 1000)
					}
				})


			},
			cutTime() {
				let self = this;
				if (t > 0) {
					t--;
					self.yzm_text = t + "S后重试"
				} else {
					t = 60;
					self.yzm_text = "获取验证码"
					clearInterval(cut)
					cut=null
				}
			},
			save() {
				this.$api.post('/merchant/save', {
					"companyProfile": this.companyProfile,
					"email": this.email,
					"enterpriseAddress": this.enterpriseAddress,
					"enterpriseBusiness": this.enterpriseBusiness,
					"enterpriseCapital": this.enterpriseCapital,
					"enterpriseCreditCode": this.enterpriseCreditCode,
					"enterpriseName": this.enterpriseName,
					"enterpriseUrl": this.enterpriseUrl,
					"identityId": this.identityId,
					"merchantCode": this.merchantCode,
					"merchantShortCode": this.merchantShortCode,
					"mobileCode": this.mobileCode,
					"mobileNo": this.mobileNo,
					"remarks": this.remarks,
					"seqId": this.seqId,
					"sex": this.sex,
					"url": this.url,
					"url1": this.url1,
					"url2": this.url2,
					"userName": this.userName,
					"type":this.type,
					"userSeqId":this.userSeqId,
					"invitedId":this.invitedId
				}).then(res => {
					if(res.code==200){
						if(is_second){
							// 再次或者添加门店
							uni.navigateTo({
								url: 'submit?seqId='+res.data
							})	
						}else {
							// 第一次
							uni.navigateTo({
								url: 'money?seqId='+res.data+"&type="+this.type
							})	
						}
						
					}
				}).catch(err=>{
					console.log(err);
				})
			},


			// 删除图片
			closeImg(item) {
				this[item] = ""
			},
			// 上传图片
			chooseImage(item) {
				let self = this;
				uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
					sourceType: ['album'],
					success: (res) => {
						var imageSrc = res.tempFilePaths[0]
						uni.uploadFile({
							url: self.api_url + '/sys/appeal/imgUpload',
							filePath: imageSrc,
							fileType: 'image',
							header: {
								Authorization: self.$common.getLocalSync('token')
							},
							name: 'file',
							success: (res) => {
								if (res.statusCode == 200) {
									let data = JSON.parse(res.data)
									if (data.code == 200) {
										let img_item='img_'+item;
										self[item] = data.data.viewUrl;
										self[img_item] = data.data.viewUrl;
										
									} else {
										this.$box.toast('上传失败', 'none')
									}
								} else {
									this.$box.toast('上传失败', 'none')
								}

							},
							fail: (err) => {
								console.log('uploadImage fail', err);
								uni.showModal({
									content: err.errMsg,
									showCancel: false
								});
							}
						});
					},
					fail: (err) => {
						console.log('chooseImage fail', err)
						// #ifdef MP
						uni.getSetting({
							success: (res) => {
								let authStatus = res.authSetting['scope.album'];
								if (!authStatus) {
									uni.showModal({
										title: '授权失败',
										content: '需要从您的相册获取图片，请在设置界面打开相关权限',
										success: (res) => {
											if (res.confirm) {
												uni.openSetting()
											}
										}
									})
								}
							}
						})
						// #endif
					}
				})
			},
			radioChange(event){
				this.sex=event.detail.value
			},
			chooseType(type) {
				this.type = type
			},
			loadMoney() {
				this.$api.get('/sysDict/list/MERCHANTRZJE001', {
					params: {}
				}).then(res => {
					if (res.code == 200) {
						this.money = res.data.value
					}
				})
			},
			
		}
	}
</script>

<style lang="scss">
	.text-red {
		color: red;
	}
	.box {
		background: white;
		width: 100%;
		min-height: calc(100vh - var(--window-top) - var(--window-bottom));
		padding-bottom: 40rpx;
		
	}

	.tab-info {
		width: calc(100% - 60rpx);
		margin: 0 auto;
		display: flex;
		height: 100rpx;
		align-items: center;
		font-size: 30rpx;
		color: #666;

		.tab-li {
			height: 60rpx;
			line-height: 60rpx;
			margin-right: 30rpx;
			cursor: pointer;
		}

		.active {
			color: #333;
			font-weight: bold;
			font-size: 32rpx;
			border-bottom: 1px solid $color-primary;
		}
	}

	.next-btn {
		width: calc(100% - 60rpx);
		background: $color-primary;
		font-size: 30rpx;
		height: 90rpx;
		line-height: 90rpx;
		text-align: center;
		color: white;
		margin: 40rpx auto;
		border-radius: 10rpx;
	}

	.info-li {
		width: calc(100% - 60rpx);
		padding: 0 30rpx;
		min-height: 100rpx;
		align-items: center;
		display: flex;
		justify-content: space-between;
		font-size: 28rpx;
		border-bottom: 1px solid #eee;

		.left {
			display: flex;
			align-items: center;
			width: 220rpx;
			flex-shrink: 0;

			.iconfont {
				font-size: 40rpx;
				margin-right: 10rpx;
				flex-shrink: 0;
			}

			.title {
				width: 180rpx;
				flex-shrink: 0;
				font-size: 30rpx;
			}
		}

		.info-li-input {
			flex-grow: 1;
			text-align: right;
			font-size: 28rpx;
			height: 100rpx;
			line-height: 100rpx;
		}
	}

	.gender-l {
		margin-right: 18rpx;
	}

	.yzm-btn {
		padding: 12rpx 20rpx;
		margin-left: 10rpx;
		width: 160rpx;
	}


	.info-photo-li {
		flex-wrap: wrap;

		.card-title {
			width: 100%;
			height: 100rpx;
			line-height: 100rpx;
			font-size: 30rpx;
			font-weight: bold;
		}
	}

	.photo_list {
		width: 100%;
		height: auto;
		display: flex;
		margin-bottom: 40rpx;
		justify-content: space-between;

		.photo_li {
			width: 220rpx;
			height: 200rpx;
			background: #F2F2F2;
			border-radius: 7rpx;
			display: flex;
			align-items: center;
			justify-content: center;
			flex-wrap: wrap;
			position: relative;

			&:last-child {
				margin-right: 0;
			}

			.photo_default {
				display: flex;
				align-items: center;
				flex-wrap: wrap;
				justify-content: center;
				cursor: pointer;
			}

			.default_icon {
				width: 53rpx;
				height: 46rpx;
				text-align: center;
				margin: 0 auto;
			}

			.default_img {
				width: 220rpx;
				height: 200rpx;
			}

			.iconcuowu {
				position: absolute;
				top: -16rpx;
				right: -10rpx;
				background: #333;
				border-radius: 100%;
				width: 36rpx;
				height: 36rpx;
				z-index: 1;
				color: white;
				font-size: 30rpx;
				text-align: center;
				line-height: 36rpx
			}

			.default_text {
				width: 100%;
				text-align: center;
				font-size: 24rpx;
				margin-top: 16rpx;
				color: #666;
			}

			.img {
				width: 100%;
				height: 100%;
			}

			.close_icon {
				width: 32rpx;
				height: 32rpx;
				position: absolute;
				top: -10rpx;
				right: -10rpx;
				z-index: 2;
				cursor: pointer;
			}
		}

	}
</style>
